.resume-title__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 36px;
}
.resume-title__container:last-child {
  margin-top: 5px;
}

.resume-title__text {
  margin: 0;
  color: #000;
  font-family: 'Space Grotesk', sans-serif;
  font-size: 188%;
  font-weight: 600;
  // line-height: 1.21;
  text-align: left;
  letter-spacing: 0.7px;
}

.resume-title__checkbox-container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.resume-title__checkbox-label {
  display: block;
  margin: 0;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.resume-title__checkbox-label .resume-title__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

.resume-title__checkbox-label .resume-title__checkbox-text {
  display: inline-block;
  position: relative;
  padding: 0 0 0 35px;
  line-height: 22px;
}

.resume-title__checkbox-label .resume-title__checkbox-text:before {
  content: '';
  display: inline-block;
  width: 22px;
  height: 22px;
  position: absolute;
  left: 0;
  top: 0;
  transition: background 0.3s ease;
  background: url(../../../../img/toggle-icon.svg) 0 0 no-repeat;
}

/* Checked */
.resume-title__checkbox-label
  .resume-title__checkbox:checked
  + .resume-title__checkbox-text:before {
  background: url(../../../../img/toggle-checked.svg) 0 0 no-repeat;
}
.resume-title__checkbox {
  width: 24px;
  height: 24px;
}

.resume-title__checkbox-text {
  color: #000;
  font-family:
    Open Sans,
    sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.21;
}
