.checkbox__container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.checkbox__label {
  display: block;
  margin: 0;
  cursor: pointer;
  user-select: none;
  position: relative;
}

.checkbox__label .checkbox__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
  padding-left: 32px;
}

.checkbox__label .checkbox__text {
  display: inline-block;
  position: relative;
  padding: 0 0 0 33px;
  line-height: 25px;
}

.checkbox__label .checkbox__text:before {
  content: '';
  display: inline-block;
  width: 60px;
  height: 30px;
  position: absolute;
  left: 0;
  top: 0;
  transition: background 0.3s ease;
  background: url(../../../../img/checkbox-icon.svg) 0 0 no-repeat;
}

.checkbox__label .checkbox__text_inactive:before {
  background: url(../../../../img/checkboxInactive.svg) 0 0 no-repeat;
}

/* Checked */
.checkbox__label .checkbox__input:checked + .checkbox__text:before {
  background: url(../../../../img/checkbox-checked.svg) 0 0 no-repeat;
}
.checkbox__input {
  width: 24px;
  height: 24px;
}

.checkbox__text {
  color: #000;
  font-family:
    Open Sans,
    sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.21;
}

.checkbox__input-inactive {
  background-color: #e6e6e6;
}
